.section__title {
  text-align: center;
  margin-bottom: 1.5rem;
}

.section__subtitle {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
}

.project__container {
  padding: 4rem;
  display: flex;
  flex-direction: column; /* Stack items vertically */
  justify-content: space-between;
  background-color: #141414;
  color: white;
  margin: 4rem;
  border-radius: 30px;
}

.project__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 15rem; /* Ensure all items have the same height */
  margin: 10px 0; /* Add margin only to the top and bottom to prevent shifting */
  background-color: rgb(38, 38, 38);
  border-radius: 15px;
  padding: 10px;
  justify-content: space-between;
}

.project__item img {
  margin-left: 8rem;
  width: 220px;
  height: 180px;
  border-radius: 10px;
  margin-right: 1.5rem;
}

.project__content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex: 1; /* Allow content to take up remaining space */
}

.project__item h2 {
  font-size: 18px;
  margin: 0.5rem 0;
  padding: 0.2rem;
  text-align: center;
}

.project__item p {
   font-size: 14px;
  width: 30rem;
  color: #fff;
  margin-bottom: 0;
  text-align: justify;
  padding: 1rem;
}

.project__item img:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
}


/* For screens up to 992px */
@media screen and (max-width: 992px) {
  .project__container {
    padding: 3rem;
    margin: 3rem;
  }

  .project__item {
    height: 13rem;
    flex-direction: column; /* Stack content vertically */
    text-align: center;
    padding: 1rem;
  }

  .project__item img {
    margin-left: 0;
    margin-bottom: 1rem;
    width: 180px;
    height: 150px;
  }

  .project__content {
    align-items: center;
  }

  .project__item h2 {
    font-size: 16px;
  }

  .project__item p {
    width: 25rem;
    font-size: 13px;
    padding: 0.8rem;
  }
}

/* For screens up to 768px */
@media screen and (max-width: 768px) {
  .project__container {
    padding: 2.5rem;
    margin: 2.5rem;
  }

  .project__item {
    height: auto;
    padding: 1rem;
  }

  .project__item img {
    width: 160px;
    height: 140px;
    display: flex;
    justify-content: center;
    margin: 1rem;
  }

  .project__item p {
    width: 100%;
    font-size: 12px;
  }
}

/* For screens up to 556px */
@media screen and (max-width: 576px) {
  .project__container {
    padding: 2rem;
    margin: 2rem;
  }

  .project__item {
    height: auto;
    padding: 0.8rem;
  }

  .project__item img {
    display: flex;
    justify-content: center;
    margin: 1rem;
    width: 140px;
    height: 120px;
  }

  .project__item h2 {
    font-size: 15px;
  }

  .project__item p {
    width: 100%;
    font-size: 11px;
    padding: 0.6rem;
  }
}

/* For screens up to 360px */
@media screen and (max-width: 360px) {
  .project__container {
    padding: 1.5rem;
    margin: 1.5rem;
  }

  .project__item {
    height: auto;
    padding: 0.5rem;
  }

  .project__item img {
    width: 120px;
    height: 100px;
    display: flex;
    justify-content: center;
    margin: 1rem;
  }

  .project__item h2 {
    font-size: 14px;
  }

  .project__item p {
    align-items: center;
    width: 100%;
    font-size: 10px;
    padding: 0.5rem;
  }
}
