.section__title {
    text-align: center;
    margin-bottom: 1.5rem;
  }
  
  .section__subtitle {
    display: block;
    text-align: center;
    margin-bottom: 4rem;
  }
  
  .contact__form {
    margin-top: 2rem;
    margin-bottom: 2rem;
    background-color: #fff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
    max-width: 500px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    padding: 2rem ;
  }
  
  .contact__form-div {
    margin-bottom: 1rem; /* Spacing between form elements */
  }
  
  .contact__form-input {
    width: 100%; /* Inputs should take the full width */
    padding: 0.75rem; /* Padding inside the inputs */
    border: 1px solid #ddd; /* Border around the inputs */
    border-radius: 5px; /* Slightly rounded corners */
    box-sizing: border-box; /* Include padding in width */
  }
  
  .button {
    display: inline-flex;
    align-items: center;
     /* Button background color */
    color: #fff; /* Button text color */
    padding: 0.75rem 1.5rem; /* Padding inside the button */
    border-radius: 5px; /* Slightly rounded corners */
    text-decoration: none; /* Remove underline from anchor */
    margin-top: 1rem; /* Space above the button */
    cursor: pointer;
    transition: background-color 0.3s; /* Smooth background color transition */
  }
  
  .button:hover {
    background-color: #333; /* Darker background on hover */
  }
  
  .button__icon {
    margin-left: 0.5rem; /* Space between text and icon */
    fill: #fff; /* Icon color */
  }

  @media screen and (max-width: 992px) {
    .section__title {
      font-size: 1.75rem;
      margin-bottom: 1.25rem;
    }
    
    .section__subtitle {
      font-size: 1.25rem;
      margin-bottom: 3rem;
    }
    
    .contact__form {
      max-width: 450px; /* Slightly reduce the max width */
      padding: 1.5rem; /* Reduce padding */
    }
    
    .contact__form-input {
      padding: 0.7rem;
    }
    
    .button {
      padding: 0.7rem 1.3rem;
    }
  }
  
  /* Styles for screens up to 768px */
  @media screen and (max-width: 768px) {
    .section__title {
      font-size: 1.5rem;
      margin-bottom: 1rem;
    }
    
    .section__subtitle {
      font-size: 1.1rem;
      margin-bottom: 2.5rem;
    }
    
    .contact__form {
      max-width: 400px; /* Reduce the max width further */
      padding: 1.25rem; /* Reduce padding */
    }
    
    .contact__form-input {
      padding: 0.65rem;
    }
    
    .button {
      padding: 0.65rem 1.2rem;
    }
  }
  
  /* Styles for screens up to 576px */
  @media screen and (max-width: 576px) {
    .section__title {
      font-size: 1.25rem;
      margin-bottom: 0.75rem;
    }
    
    .section__subtitle {
      font-size: 1rem;
      margin-bottom: 2rem;
    }
    
    .contact__form {
      max-width: 350px; /* Further reduce the max width */
      padding: 1rem; /* Reduce padding */
    }
    
    .contact__form-input {
      padding: 0.6rem;
    }
    
    .button {
      padding: 0.6rem 1rem;
    }
  }
  
  /* Styles for screens up to 360px */
  @media screen and (max-width: 360px) {
    .section__title {
      font-size: 1.1rem;
      margin-bottom: 0.5rem;
    }
    
    .section__subtitle {
      font-size: 0.9rem;
      margin-bottom: 1.5rem;
    }
    
    .contact__form {
      max-width: 300px; /* Further reduce the max width */
      padding: 0.75rem; /* Reduce padding */
    }
    
    .contact__form-input {
      padding: 0.5rem;
    }
    
    .button {
      padding: 0.5rem 0.9rem;
      font-size: 0.9rem;
    }
  }