.edu__content {
    display: flex;
    flex-direction: row;
    margin: 0 4rem 0 4rem;
    border-bottom: 2px solid grey;
    padding-bottom: 2rem;
}

.edu__stream {
    font-weight: 500;
}

.edu__para {
    display: flex;
    font-size: 1rem;
}

.resume__container {
    margin-top: 2rem;
}

.edu__title {
    flex-basis: 50%;
    padding: 2rem;
    font-size: 1.4rem;
    font-weight: 500;
}

.time__content {
    padding-bottom: 1rem;
    display: flex;
    flex: 1;
    margin-right: 2rem;
    color: rgb(50, 47, 47);
}

.edu__college {
    font-size: 1rem;
    font-style: italic;
    font-weight: 400;
    color: rgb(11, 108, 220);
}

.edu___time {
    font-size: 0.8rem;
    padding-left: 1rem;
    font-weight: 400;
    color: rgb(50, 47, 47);
}

.edu__subtitle {
    text-align: left;
    justify-content: center;
    padding: 2rem;
}

.experience_2 {
    padding-top: 4rem;
}

.edu__location {
    font-size: 0.8rem;
    padding-left: 1rem;
    font-weight: 400;
    color: rgb(50, 47, 47);
}

/* Media queries for responsive design */

/* For screens less than or equal to 992px */
@media (max-width: 992px) {
    .edu__content {
        flex-direction: column;
        margin: 0 2rem;
        align-items: center;
    }
    .time__content {
        align-items: center;
        margin-right: 0;
    }
    .edu__title, .edu__subtitle {
        text-align: center;
        padding: 1rem;
    }
    .edu__college {
        align-items: center;
        margin-right: 0;
    }

    .edu__para, .edu__college, .edu___time, .edu__location{
        font-size: 1rem;
        align-items: center;
        text-align: left;
        margin-right: 0;   
    }
  
}

/* For screens less than or equal to 768px */
@media (max-width: 768px) {
    .edu__content {
        margin: 0 1.5rem;
        padding: 1rem;
        align-items: center;

    }
    .edu__title, .edu__subtitle {
        padding: 1rem 0.5rem;
        text-align: center;
    }

    .time__content {
        font-size: 0.5rem;
        align-items: center;
        margin-right: 0;
    }
}

/* For screens less than or equal to 552px */
@media (max-width: 576px) {
    .edu__content {
        
        margin: 0 1rem;
        padding: 1rem;
    }
    .edu__title, .edu__subtitle {
        padding: 0.5rem;
    }
    .time__content {
        padding-top: 0.5rem;
        font-size: 0.5rem;
        align-items: center;
        margin-right: 0;
    }
    .edu__para, .edu__college, .edu___time, .edu__location{
        font-size: 0.8rem;
        align-items: center;
        text-align: left;
        margin-right: 0;   
    }
}


/* For screens less than or equal to 356px */
@media (max-width: 360px) {

    .edu__content {
        margin: 0 0.5rem;
        padding: 1rem;
    }
    .edu__title, .edu__subtitle {
        font-size: 0.8rem;
        padding: 0.5rem;
    }
    .edu__para, .edu__college, .edu___time, .edu__location{
        font-size: 0.6rem;
        align-items: center;
        margin-right: 0;   
    }
}
