.home__content{
    margin-top: 2rem;
    grid-template-columns: 116px repeat(2,1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social{
 display: flex;
 justify-content: space-between;
 margin-right: 2rem;
 gap: 1rem;
}

.home__social-icon{
   
    width: 2.5rem; 
    height: 2.5rem; /* Icon height */
    line-height: 2.5rem; 
    border-radius: 50%; /* Circular background */
    color: black; /* White icon color */
    font-size: 1.5rem; /* Icon size */
    transition: background-color 0.3s ease;
}

.home__social-icon:hover{
    font-size: 1.5rem;
    color: #1e90ff;
    transition: all 0.3s ease-in-out;
}

.home__title{
    padding-right: 7rem;
    font-size: 2rem;
    margin-bottom: 1rem;
}


.home__hand{
    width: 35px;
    height: 35px;
    margin-left: 0.8rem;
}

.home__subtitle{
    position: relative;
    font-size: 1.3rem;
    padding-left: 11.2rem;
    padding-right: -1rem;
    font-weight: 300;
    margin-bottom: 1rem;
}
.home__subtitle::before {
    content: '';
    position: absolute;
    width: 150px; /* Default width */
    height: 1px;
    background-color: black;
    left: 0; /* Fixed at 0 to align with the subtitle */
    top: 1rem;
}

.home__description{
    max-width: 400px;
    margin-bottom: 2.5rem;
  text-align: justify;
}

.home__img{
    background: url('../../assets/profile.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgb(225 225 225 / 30%);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    animation: profile__animate 8s ease-in-out infinite 1s;
}

.home__cv{
    
    display: flex;
    margin: auto;
    padding: 1rem 1.2rem;
    border-radius: 2px 2px  ;
    border: 1px solid black;
    
}
.home__cv:hover{
 color: blue;
}

@keyframes profile__animate {
    0%{
        border-radius: 50% 40% 30% 70%/60% 30% 70% 40%;
    }
    
    50%{
        border-radius: 30% 60% 70% 40%/60% 60% 30% 60%;
    }
    100%{
        border-radius: 50% 40% 30% 70%/60% 30% 70% 40%;
    }
    
}

.container_img {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Button styling */
  .button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #0056b3;
  }
  
  .button__icon {
    margin-left: 8px;
  }
  
  /* SVG styling */
  svg {
    fill: #fff;
    width: 24px;
    height: 24px;
  }




@media screen and (max-width: 992px) {
    .home__content {
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
        padding-top: 4rem;
    }
    
    .home__social {
        margin-left: 4rem;
        justify-content: center;
    }

    .home__title {
        padding-right: 0;
        font-size: 1.75rem;
        margin-bottom: 1rem;
    }

    .home__hand {
        width: 30px;
        height: 30px;
        margin-left: 0.5rem;
    }

    .home__subtitle {
        padding-left: 0;
        padding-right: 0;
        font-size: 1.1rem;
        margin-bottom: 1rem;
    }

    .home__subtitle::before {
        width: 100px;
        left: 0;
        top: 0.8rem;
    }

    .home__description {
        max-width: 100%;
        margin-bottom: 2rem;
        padding: 0 1rem;
        text-align: justify;
    }

    .home__img {
        width: 250px;
        height: 250px;
    }
}

/* Media Query for screens up to 768px */
@media screen and (max-width: 768px) {
    .home__content {
        grid-template-columns: 1fr;
        text-align: center;
        padding-top: 1.5rem; /* Reduce the padding-top */
        margin-top: -2.5rem; 
        padding-bottom: 2rem;
        column-gap: 4rem;
    }

    .home__social {
        padding-bottom: 2rem;
        margin-left: 4rem;
        display: flex;
       
        align-items: center;
        justify-content: center;
    }

    .home__title {
        padding-right: 0;
        font-size: 1.5rem;
        margin-bottom: 1rem;
    }

    .home__hand {
        width: 30px;
        height: 30px;
        margin-left: 0.5rem;
    }

    .home__subtitle {
        padding-left: 0;
        padding-right: 0;
        font-size: 1rem;
        margin-bottom: 1rem;
    }

    .home__subtitle::before {
        left: 15%;
        width: 100px;
        top: 0.8rem;
    }

    .home__description {
        max-width: 600px;
        margin: auto;
        margin-bottom: 2rem;
        padding: 0 4rem;
        text-align: justify;
    }

    .home__img {
        width: 200px;
        height: 200px;
        order: initial;
        justify-self: initial;
        margin: 0 auto;
        box-shadow: inset 0 0 0 5px rgb(225 225 225 / 30%);
    }
    .home__social-icon {
        margin-bottom: 0.5rem;
        padding-right: 2rem;
    }
}

@media screen and (max-width: 556px) {
    .home__content {
        grid-template-columns: 1fr;
        text-align: center;
        padding-top: 1.5rem; /* Reduce the padding-top */
        margin-top: -1.5rem; /* Apply a negative margin to shift content upward */
        column-gap: 0.5rem;
        
    }

    .home__social {
        margin-left: 4rem;
        justify-content: center;
        flex-direction: row;
        align-items: center;
    }

    .home__social-icon {
        margin-bottom: 0.5rem;
        padding-right: 2rem;
    }

    .home__title {
        padding-right: 0;
        font-size: 1.25rem;
        margin-bottom: 1rem;
    }

    .home__hand {
        width: 25px;
        height: 25px;
        margin-left: 0.5rem;
    }

    .home__subtitle {
        padding-left: 0;
        padding-right: 0;
        font-size: 0.9rem;
        margin-bottom: 1rem;
    }

    .home__subtitle::before {
        left: 15%;
        width: 60px;
        top: 0.7rem;

    }

    .home__description {
        max-width: 400px;
        margin-bottom: 1rem;
        padding: 2rem 2rem 2rem 2rem;
        text-align: justify;
        margin: auto;
    }

    .home__img {
        width: 150px;
        height: 150px;
        order: initial;
        justify-self: initial;
        margin: 0 auto;
        box-shadow: inset 0 0 0 5px rgb(225 225 225 / 30%);
    }
}


@media screen and (max-width: 360px) {
    .home__content {
        grid-template-columns: 1fr;
        text-align: center;
        padding-top: 1.5rem; /* Reduce the padding-top */
        margin-top: -2.5rem; /* Apply a negative margin to shift content upward */
        column-gap: 0.5rem;
    }

    .home__social {
        margin-left: 4rem;
       
        justify-content: center;
        flex-direction: row;
        align-items: center;
    }

    .home__social-icon {
        margin-bottom: 0.5rem;
        padding-right: 2rem;
    }

    .home__title {
        padding-right: 0;
        font-size: 1.1rem;
        margin-bottom: 1rem;
    }

    .home__hand {
        width: 20px;
        height: 20px;
        margin-left: 0.5rem;
    }

    .home__subtitle {
        padding-left: 0;
        padding-right: 0;
        font-size: 0.8rem;
        margin-bottom: 1rem;
    }

    .home__subtitle::before {
        left: 20%;
        width: 40px;
        top: 0.7rem;
    }

    .home__description {
        max-width: 300px;
        margin-bottom: 1rem;
        padding: 1rem;
        text-align: justify;
        margin-left: 2rem;
    }

    .home__img {
        width: 120px;
        height: 120px;
        order: initial;
        justify-self: initial;
        margin: 0 auto;
        box-shadow: inset 0 0 0 4px rgb(225 225 225 / 30%);
    }
}