.skills__frontend {
    display: flex;
    justify-content: space-evenly;
    padding: 4rem 8rem 2rem 8rem;
}

.skills__backend {
    display: flex;
    justify-content: space-evenly;
    padding: 2rem 8rem 2rem 6rem;
}

.skills__content{
    padding: 1rem;
}

.skills__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 2rem 0 2rem;
}

.skills__title {
    font-size: 1rem;
    margin-top: 0.5rem;
    font-weight: 400;
}


/* Responsive for screens <= 992px */
@media (max-width: 992px) {
    .skills__frontend, .skills__backend {
        padding: 3.5rem 6rem 2rem 6rem;
    }

    .skills__container {
        padding: 0 1.5rem 0 1.5rem;
    }

    .skills__title {
        font-size: 0.95rem;
    }
}

/* Responsive for screens <= 768px */
@media (max-width: 768px) {
    .skills__frontend, .skills__backend {
        flex-direction: column;
        align-items: center;
        padding: 3rem 4rem 2rem 4rem;
    }

    .skills__container {
        flex-direction: row;
        padding: 0 1rem 0 1rem;
    }

    .skills__title {
        font-size: 0.9rem;
        margin-bottom: 0.5rem;
    }


    .skills__content{
        padding-bottom: 0.5rem;
    }
}

/* Responsive for screens <= 556px */
@media (max-width: 576px) {
    .skills__frontend, .skills__backend {
        flex-direction: column;
        align-items: center;
        padding: 2.5rem 2.2rem 1.5rem 3rem;
    }

    .skills__container {
        flex-direction: row;
        padding: 0 0.75rem 0 0.75rem;
    }

    .skills__title {
        font-size: 0.85rem;
        margin-bottom: 0.5rem;
    }
}

/* Responsive for screens <= 356px */
@media (max-width: 360px) {
    .skills__frontend, .skills__backend {
        flex-direction: column;
        align-items: center;
        padding: 2rem 2rem 1rem 2rem;
    }

    .skills__container {
        padding: 0 0.5rem 0 0.5rem;
    }

    .skills__title {
        font-size: 0.8rem;
        margin-bottom: 0.5rem;
    }
}
